import { Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { DatabaseService } from 'src/app/shared/db/database.service';
import { ListItemModel, ProductReview, SummaryBenefits, SummaryNegatives } from 'src/app/shared/models/models';
import {butterService} from '../../../services';

@Component({
  selector: 'app-product-review',
  templateUrl: './product-review.component.html',
  styleUrls: ['./product-review.component.scss']
})
export class ProductReviewComponent implements OnInit {

  public post: any;

  public productReview: ProductReview;
  public productOverview: ListItemModel = {
    Name: '',
    LogoImage: '',
    ProductImage: '',
    Title: '',
    HeritageScore: 0,
    UsabilityScore: 0,
    GenealogyScore: 0,
    CustomerSupportScore: 0,
    Price: 0,
    ResultTime: 0,
    SoldTests: 0,
    ResellsDNA: false,
    ShowDailyClicks: false,
    AffiliateUrl: '',
    ReviewUrl: '',
    generalScore: ListItemModel.prototype.generalScore
  };
  public showMore: boolean = false;
  public summaryBenefits: SummaryBenefits[] = [];
  public summaryNegatives: SummaryNegatives[] = [];
  public updatedAt: string;

  public sticky: boolean = false;
  public stickyPosition: any;
  @ViewChild('stickyMenuStart') stickyMenuStart: ElementRef;
  @ViewChild('top') topElement: ElementRef;
  constructor(public router: Router, public db: DatabaseService, private title: Title, private metaService: Meta) {
  }

  public async ngOnInit() {
    var res = await this.fetchPosts();

    this.title.setTitle(res.data.data.fields.seo.title);

    this.metaService.updateTag(
      { name: 'description', content: res.data.data.fields.seo.meta_description });
      this.metaService.updateTag(
      { name: 'robots', content: 'index, follow' });

    if (this.stickyPosition) {
      this.stickyPosition = this.stickyMenuStart.nativeElement.offsetTop - this.topElement.nativeElement.offsetTop;
    }
  }

  ngAfterViewInit(){
    this.stickyPosition = this.stickyMenuStart.nativeElement.offsetTop - this.topElement.nativeElement.offsetTop;
}

  @HostListener('window:scroll', ['$event'])
  handleScroll(){

    const windowScroll = window.pageYOffset;

    if(windowScroll >= this.stickyPosition){
        this.sticky = true;
    }
     else {
        this.sticky = false;
    }
}

private async fetchPosts() : Promise<any> {
  var params = {
    "preview": 0,
    "page": 1,
    "page_size": 10,
    "locale": 'en',
    "levels": 2
};


  var slug = this.router.url.split('/')[this.router.url.split('/').length - 1];
  var res = await butterService.page.retrieve('recension', slug);
          this.post = res.data;
          console.log(res);

          //Parse benefits and negatives
          var benefits = this.post.data.fields.product_review.benefitsandnegatives;
          var list: string[] = benefits.split(",");
          for (let p of list) {
              if (p.indexOf(':') !== -1) {
                  var divide = p.split(":");
                  if (divide && divide.length > 0) {
                    if (divide[1] == "true") {
                      this.summaryBenefits.push({ Text: divide[0] });
                    }
                    else {
                      this.summaryNegatives.push({ Text: divide[0] });
                    }

                  }
              }
          }

          //Parse updated at time
          this.updatedAt = this.post.data.updated.split("T")[0];
          this.productOverview = this.db.listItems.find(x => x.Name == this.post.data.fields.product_review.name_of_product);
          return res;
}

}
