export class ListItemModel {
    Name: string;
    LogoImage: string;
    ProductImage: string;
    Title: string;
    HeritageScore: number;
    UsabilityScore: number;
    GenealogyScore: number;
    CustomerSupportScore: number;
    Price: number;
    ResultTime: number;
    SoldTests: number;
    ResellsDNA: boolean;
    ShowDailyClicks: boolean;
    AffiliateUrl: string;
    ReviewUrl?: string;

    constructor() {
    }

    generalScore(): number {
        return (this.HeritageScore + this.UsabilityScore + this.GenealogyScore + this.CustomerSupportScore) / 4;
    }
 
}

export class ProductReview {
    Name: string;
    Url: string;
    Catcher: string;
    Summary: string[];
    Benefits: SummaryBenefits[];
    Negatives: SummaryNegatives[];
    PostCategories: PostCategory[];
    LatestUpdate: string;
}

export class SummaryBenefits {
    Text: string;
}

export class SummaryNegatives {
    Text: string;
}

export class PostCategory {
    Title: string;
    Text: string;
}

export class BlogPostPreview {
    Title: string;
    Preview: string;
    Url: string;
    Image?: string;
}