import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DatabaseService } from '../shared/db/database.service';
import { ListItemModel } from '../shared/models/models';

@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.scss']
})
export class ListItemComponent implements OnInit {

  @Input() listItem: ListItemModel;
  @Input() listItemName: string;
  @Input() public showMore: boolean = true;
  @Input() public index: number = 0;
  @Input() public showBreakDown: boolean = true;
  constructor(private db: DatabaseService) { }

  ngOnInit(): void {
    //If list item name get it from db
    if (this.listItemName) {
      this.listItem = this.db.listItems.find(x => x.Name == this.listItemName);
    }
  }

}
