import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  @Output() public sidenavToggle = new EventEmitter();
  @Input() public menuOpen: boolean = true;
  public activePage: string;

    @Input()
  get mnuOpen(): boolean { return this.menuOpen; }
  set mnuOpen(name: boolean) {
    this.menuOpen = !this.menuOpen;
    console.log(this.menuOpen);
  }

  constructor(private router: Router) {
    this.router.events.subscribe(event => {
      if(event instanceof NavigationEnd){
        this.setAsActive();
      }
    });
   }

  ngOnInit(): void {
    this.setAsActive();
  }



  public onToggleSidenav(event) {
    this.sidenavToggle.emit(event);
 }

 public setAsActive(active = null) {
    if (!active) {
      var url = window.location.pathname;
      url = url.slice(1);
      for (let r of this.router.config) {
        if (url == r.path) this.activePage = r.path; 
      }
    }
    else {
      this.activePage = active;
    }
 }

 public goHome() {
   this.router.navigate(['/']);
 }

}
