import { BrowserModule, Meta } from '@angular/platform-browser';
import { APP_INITIALIZER, NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {MatToolbarModule} from '@angular/material/toolbar';
import { RoutingModule } from './routing/routing.module';
import { HomeComponent } from './home/home.component';
import { MatSidenavModule } from '@angular/material/sidenav';
import { HeaderComponent } from './navigation/header/header.component';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { FlexLayoutModule } from '@angular/flex-layout';;
import { MatTabsModule } from '@angular/material/tabs';
import { MatListModule } from '@angular/material/list';
import { ListItemComponent } from './list-item/list-item.component';
import { RatingComponent } from './rating/rating.component';
import { CircleRatingComponent } from './circle-rating/circle-rating.component';
import { MyheritageComponent } from './pages/myheritage/myheritage.component';
import { StickyItemComponent } from './sticky-item/sticky-item.component';
import { LivingdnaComponent } from './pages/reviews/livingdna/livingdna.component';
import { ProductReviewComponent } from './pages/reviews/product-review/product-review.component';
import { Router, RouterModule } from '@angular/router';
import { DatabaseService } from './shared/db/database.service';
import { PostComponent } from './pages/posts/post/post.component';

export function initApp (db: DatabaseService, router: Router) {
  return async () => router.config.concat(await db.fetch());
}

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    HeaderComponent,
    ListItemComponent,
    RatingComponent,
    CircleRatingComponent,
    MyheritageComponent,
    StickyItemComponent,
    LivingdnaComponent,
    ProductReviewComponent,
    PostComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    NgbModule,
    MatToolbarModule,
    MatSidenavModule,
    MatIconModule,
    MatButtonModule,
    FlexLayoutModule,
    MatTabsModule,
    MatListModule,
    RoutingModule
  ],
  exports: [
    MatSidenavModule,
    MatIconModule,
    MatButtonModule,
    FlexLayoutModule,
    MatTabsModule,
    MatListModule
  ],
  providers: [ 
    { provide: APP_INITIALIZER, useFactory: initApp, deps: [DatabaseService, Router], multi: true }, 
    Meta
],
  bootstrap: [AppComponent]
})
export class AppModule { }
