import { Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { DatabaseService } from 'src/app/shared/db/database.service';
import { ListItemModel, ProductReview, SummaryBenefits, SummaryNegatives } from 'src/app/shared/models/models';
import {butterService} from '../../../services';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.scss']
})
export class PostComponent implements OnInit {

  public post: any;
  public listItems: ListItemModel[] = [];
  public productOverview: ListItemModel = {
    Name: '',
    LogoImage: '',
    ProductImage: '',
    Title: '',
    HeritageScore: 0,
    UsabilityScore: 0,
    GenealogyScore: 0,
    CustomerSupportScore: 0,
    Price: 0,
    ResultTime: 0,
    SoldTests: 0,
    ResellsDNA: false,
    ShowDailyClicks: false,
    AffiliateUrl: '',
    ReviewUrl: '',
    generalScore: ListItemModel.prototype.generalScore
  };
  public showMore: boolean = false;
  public summaryBenefits: SummaryBenefits[] = [];
  public summaryNegatives: SummaryNegatives[] = [];
  public updatedAt: string;

  public sticky: boolean = false;
  public stickyPosition: any;
  public stickyStopPosition: any;
  @ViewChild('stickyMenuStart') stickyMenuStart: ElementRef;
  @ViewChild('stickyMenuStop') stickyMenuStop: ElementRef;
  @ViewChild('top') topElement: ElementRef;
  constructor(public router: Router, public db: DatabaseService, private metaService: Meta, private title: Title) {
  }

  public async ngOnInit() {
    this.listItems = this.db.listItems.slice(0, 2);
    var res = await this.fetchPosts();

    this.title.setTitle(res.data.data.seo_title);

    this.metaService.updateTag(
      { name: 'description', content: res.data.data.meta_description });
      this.metaService.updateTag(
      { name: 'robots', content: 'index, follow' });
    
    if (this.stickyPosition) {
      this.stickyPosition = this.stickyMenuStart.nativeElement.offsetTop - this.topElement.nativeElement.offsetTop;
    }
  }

  ngAfterViewInit(){
    this.stickyPosition = this.stickyMenuStart.nativeElement.offsetTop - this.topElement.nativeElement.offsetTop;
}

  @HostListener('window:scroll', ['$event'])
  handleScroll(){

    const windowScroll = window.pageYOffset;

    this.stickyStopPosition = this.stickyMenuStop.nativeElement.offsetTop - 50;

    if(windowScroll >= this.stickyPosition && windowScroll < this.stickyStopPosition){
        this.sticky = true;
    }
     else {
        this.sticky = false;
    }

    if (this.router.url == '/om-oss') {
      this.sticky = false;
    }
}

private async fetchPosts(): Promise<any> {
    var params = {
      "preview": 0,
      "page": 1,
      "page_size": 10,
      "locale": 'en',
      "levels": 2
    };

    var slug = this.router.url.split('/')[this.router.url.split('/').length - 1];
    var res = await butterService.post.retrieve(slug);
    this.post = res.data;
    console.log(res);
    //Parse updated at time
    this.updatedAt = this.post.data.updated.split("T")[0];
    this.productOverview = this.db.listItems[0];
    return res;
  }

}
